/*==========STYLES===========*/
.our--process-area .seo--title h1 {
    color: #246DFB;
    font-weight: 600;
}

.our--process-feedback {
  padding: 60px 40px;
  display: flex;
  transition: all .3s linear;
  border-radius: 40px;
  height: 380px;
  width: 380px;
}

.col_scrum {
  background : linear-gradient(0deg, rgba(41, 115, 220, 1) 0%, rgba(0, 255, 255, 1) 100%);
  background : -moz-linear-gradient(50% 100% 90deg,rgba(41, 115, 220, 1) 0%,rgba(0, 255, 255, 1) 100%);
  background : -webkit-linear-gradient(90deg, rgba(41, 115, 220, 1) 0%, rgba(0, 255, 255, 1) 100%);
  background : -o-linear-gradient(90deg, rgba(41, 115, 220, 1) 0%, rgba(0, 255, 255, 1) 100%);
  background : -ms-linear-gradient(90deg, rgba(41, 115, 220, 1) 0%, rgba(0, 255, 255, 1) 100%);
}

.col_devops {
    background : linear-gradient(210deg, rgba(41, 115, 220, 1) 0%, rgba(40, 118, 221, 1) 20.02%, rgba(37, 129, 223, 1) 36.76%, rgba(32, 146, 228, 1) 52.33%, rgba(25, 171, 234, 1) 67.19%, rgba(15, 202, 242, 1) 81.55%, rgba(4, 240, 251, 1) 95.34%, rgba(0, 255, 255, 1) 100%);
    background : -moz-linear-gradient(80.32% -6.8% -120deg,rgba(41, 115, 220, 1) 0%,rgba(40, 118, 221, 1) 20.02%,rgba(37, 129, 223, 1) 36.76%,rgba(32, 146, 228, 1) 52.33%,rgba(25, 171, 234, 1) 67.19%,rgba(15, 202, 242, 1) 81.55%,rgba(4, 240, 251, 1) 95.34%,rgba(0, 255, 255, 1) 100%);
    background : -webkit-linear-gradient(-120deg, rgba(41, 115, 220, 1) 0%, rgba(40, 118, 221, 1) 20.02%, rgba(37, 129, 223, 1) 36.76%, rgba(32, 146, 228, 1) 52.33%, rgba(25, 171, 234, 1) 67.19%, rgba(15, 202, 242, 1) 81.55%, rgba(4, 240, 251, 1) 95.34%, rgba(0, 255, 255, 1) 100%);
}

.col_devops .clients--feedback-item .feedback--body  {
    text-align: left;
}
.col_scrum .clients--feedback-item .feedback--body  {
    text-align: right;
}

.clients--feedback-item .feedback--body .f_name {
    font-size: 40px;
    color: #fff;
    line-height: 1.1;
    font-weight: 600;
    letter-spacing: 0em;
    margin-bottom: 20px;
  }

  .clients--feedback-item .feedback--body {
    position: relative;
    flex: 1;
  }

  .our--process-feedback.col_scrum:before {
    content: "";
    background : -moz-linear-gradient(50% -82.43% -90deg,rgba(244, 207, 87, 1) 0%,rgba(252, 115, 70, 1) 99.09%);
    background : -webkit-linear-gradient(-90deg, rgba(244, 207, 87, 1) 0%, rgba(252, 115, 70, 1) 99.09%);
    background : -o-linear-gradient(-90deg, rgba(244, 207, 87, 1) 0%, rgba(252, 115, 70, 1) 99.09%);
    background : -ms-linear-gradient(-90deg, rgba(244, 207, 87, 1) 0%, rgba(252, 115, 70, 1) 99.09%);
    background : linear-gradient(180deg, rgba(244, 207, 87, 1) 0%, rgba(252, 115, 70, 1) 99.09%);
    position: absolute;
    display: inline-block;
    left: 0;
    border-radius: 40px 28px 95%;
    width: 310px;
    height: 250px;
    background-size: cover;
    z-index: 0;
    top: 0px;
  }
  
  .our--process-feedback.col_devops:before {
    content: "";
    background : -moz-linear-gradient(50% -11.35% -90deg,rgba(173, 54, 251, 1) 0%,rgba(148, 38, 251, 1) 100%);
    background : -webkit-linear-gradient(-90deg, rgba(173, 54, 251, 1) 0%, rgba(148, 38, 251, 1) 100%);
    background : -o-linear-gradient(-90deg, rgba(173, 54, 251, 1) 0%, rgba(148, 38, 251, 1) 100%);
    background : -ms-linear-gradient(-90deg, rgba(173, 54, 251, 1) 0%, rgba(148, 38, 251, 1) 100%);
    background : linear-gradient(180deg, rgba(173, 54, 251, 1) 0%, rgba(148, 38, 251, 1) 100%);
    position: absolute;
    display: inline-block;
    right: 0;
    border-radius: 50% 40px 40px 80% / 40% 40px 40px 50%;
    width: 270px;
    height: 300px;
    background-size: cover;
    z-index: 0;
    top: 0px;
  }


  .clients--feedback-item .feedback--body p {
    line-height: 30px;
    margin-bottom: 15px;
  }

  .col_devops .feedback--body .read--more-btn {
    color: #AD36FB;
  }
  
  .col_scrum .feedback--body .read--more-btn {
    color: #F4CF57;
  }

  
  .feedback--body  .read--more-btn:hover, .feedback--body .read--more-btn:hover:before{
    color: #fff;
  }




/*==========ANIMATION===========*/

/*==========RESPONSIVE===========*/
/*Mobil*/
@media (max-width: 512px) { 
    .our--process-feedback {
        margin: 15px 0 !important;
        padding: 0px!important;      
    }
    .col_devops .clients--feedback-item:before {
      right: 15px;
    }

    .col_devops {
        margin-top: 40px;
    }

    .col_scrum .clients--feedback-item:before {
        left: 15px;
    }
    .our--process-area h2 {
      font-size: 35px;
    }

    .clients--feedback-item {
      padding: 40px 30px;;
  }



}
/*Tablet*/
@media (min-width: 513px) and (max-width:821px){
  .our--process-feedback {
    margin: 15px 0 !important;
  }

  .clients--feedback-item .feedback--body p {
    line-height: 20px;
  }
}
